














import { Component, Vue } from 'vue-property-decorator';
import { ajaxUserBindWechatUser, ajaxUserUnBindWechatUser } from '@/server';
import WeuiToast from '@/components/WeuiToast.vue';

// 定义接口*****************
interface Message {
  account: string;
  accountPlaceholder: string;
  password: string;
  passwordPlaceholder: string;
}
// 声明组件*****************
@Component({
  components: {
    WeuiToast
  }
})
export default class BindJupiter extends Vue {
  // 属性*****************
  private bingData: Message = {
    account: '',
    accountPlaceholder: '请输入您的账号/手机号',
    password: '',
    passwordPlaceholder: '请输入您的密码',
  };
  private pageType: string = 'bind';
  private wx: any = (window as any).wx;
  private WeuiToastMessage: boolean = false;
  private type: string = 'loading';
  // 挂载完成*****************
  mounted ():void {
    this.pageType = this.$route["query"]["pagetype"] == "unbundling" ? "unbundling" : "bind";
  }

  // 方法*****************
  // 提交绑定
  private bind(): void {
    if(this['bingData']['account']&&this['bingData']['password']){
      this['type'] = 'loading';
      this.WeuiToastMessage = true;
      if(this['$route']['query']['pagetype']==='unbundling'){// 解绑
        this.unBindAccount();
      }else{// 绑定
        this.bindAccount();
      }
    }else{
      alert('请填写完整');
    }
  }

  // 绑定账号
  private bindAccount(): void {
      ajaxUserBindWechatUser({
        info_user:{
          account: this['bingData']['account'],
          password: this['bingData']['password'],
          open_id: this['$route']['query']['open_id']
        }
      }).then((res: object)=>{
        this.wx.ready(()=>{
          this['type'] = 'success';
          setTimeout(()=>{
            this.wx.closeWindow();
            this.WeuiToastMessage = false;
          },1000)
        });
      }).catch((error: any)=>{
        if(error['code'] === 6994){
          alert('密码错误');
          this.WeuiToastMessage = false;
        }else if(error['code'] === 6993){
          alert('用户不存在');
          this.WeuiToastMessage = false;
        }else if(error['code'] === 6987){
          alert('此账号已绑定微信');
          this.WeuiToastMessage = false;
        }else{
          this.WeuiToastMessage = false;
        }
        console.log('error:',error)
      })
  }

  // 解绑账号
  private unBindAccount(): void {
      ajaxUserUnBindWechatUser({
          account: this['bingData']['account'],
          password: this['bingData']['password'],
          open_id: this['$route']['query']['open_id']
      }).then((res: object)=>{
        this.wx.ready(()=>{
          this['type'] = 'success';
          setTimeout(()=>{
            this.wx.closeWindow();
            this.WeuiToastMessage = false;
          },1000)
        });
      }).catch((error: any)=>{
        if(error['code'] === 6994){
          alert('密码错误');
          this.WeuiToastMessage = false;
        }else if(error['code'] === 6993){
          alert('此账号暂未绑定微信');
          this.WeuiToastMessage = false;
        }else if(error['code'] === 6987){
          alert('此账号已绑定微信');
          this.WeuiToastMessage = false;
        }else{
          this.WeuiToastMessage = false;
        }
        console.log('error:',error)
      })
  }

  // 清空密码
  private emptyPassword(): void {
    this.bingData.password = '';
  }

}
