import axios from 'axios';
// CancelToken 取消ajax
// 全局的配置的默认值/defaults
const axiosConfig = () => {
    axios.defaults.baseURL = '/api';
    axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
    axios.defaults.timeout = 60000;
};
axiosConfig();

/*** 在数据请求回来之后的公共判断 */
const ajaxAftet = (result: any) => {
    return new Promise((resolve, reject) => {
        if (result.status === 200) {
            if (result.data.code === 0) {
                resolve(result.data);
            } else {
                reject(result.data);
            }
        } else {
            // console.log('网络错误');
            reject(result.data);
        }
    });
};
/****
 * get 请求   用于请求列表，请求详情
 */
export const ajaxGet = (url: string, data: object) => {
    return axios.get(url, {
        params: data,
    }).then((result: object) => ajaxAftet(result));
};
/****
 * post 请求   用于新增
 */
export const ajaxPost = (url: string, data: object) => {
    return axios.request({
        url,
        method: 'post',
        params: data,
        data,
    }).then((result: object) => ajaxAftet(result));
};

/**
 * delete 请求   用于删除
 */
export const ajaxDelete = (url: string, data: object) => {
    return axios.request({
        url,
        method: 'delete',
        params: data,
    }).then((result: object) => ajaxAftet(result));
}

/** 绑定账号 */
export const ajaxUserBindWechatUser = (data: object) => ajaxPost('/api_proxy/user/bind_wechat_user/', data);

/** 解绑账号 */
export const ajaxUserUnBindWechatUser = (data: object) => ajaxDelete('/api_proxy/user/bind_wechat_user/', data);

/** 报警查询列表 */
export const ajaxEventWechatEventList = (data: object) => ajaxGet('/api_proxy/event/wechat_event_list/', data);
